import React from 'react';
import './Auth.scss';
import { Input } from './Input';

class NewAuth extends React.Component {
	render() {
		return (
			<form onSubmit={(e) => {e.preventDefault()}} className="form">
				<Input type="text" label=" " name="login" />
				<Input type="password" label="" name="password" />
				<Input type="password" label=" " name="password-repeat" />
				<input type="submit" value="" className="form-button" />
			</form>
		)
	}
}

export default NewAuth;